import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import ToolsTWS from "./ToolsTWS";
import ToolsScanners from "./ToolsScanners";
import ToolsInfo from "./ToolsInfo";
import ToolsAside from "./ToolsAside";
import TradingView from "./TradingView";
import ToolsTradelog from "./ToolsTradelog"

function Tools() {
  return (
    <div className="columns">
      <div className="column is-three-quarters">
        <Router>
          <Switch>
            <Route exact path="/tools" component={ToolsInfo}></Route>
            <Route
              exact
              path="/tools/scanners"
              component={ToolsScanners}
            ></Route>
            <Route path="/tools/tws" component={ToolsTWS}></Route>
            <Route path="/tools/tradelog" component={ToolsTradelog}></Route>
          </Switch>
        </Router>
      </div>
      <div className="column is-one-quarter">
        <ToolsAside></ToolsAside>
      </div>
    </div>
  );
}

export default Tools;
