function parseList() {
  const list =
    "NASDAQ:ADBE,NYSE:AI,NYSE:ALL,NASDAQ:AMD,NASDAQ:AMGN,NASDAQ:AMZN,NASDAQ:APPS,NASDAQ:ATVI,NYSE:AVTR,NYSE:BA,NYSE:BABA,NASDAQ:BILI,NYSE:BILL,NASDAQ:BLNK,NASDAQ:BYND,NASDAQ:CAKE,NYSE:CAT,NYSE:CHGG,NYSE:CLDR,NYSE:COP,NASDAQ:COST,NYSE:CRM,NASDAQ:CRWD,NYSE:CVS,NYSE:DASH,NASDAQ:DDOG,NYSE:DIS,NASDAQ:DKNG,NASDAQ:EA,NASDAQ:EXPE,NASDAQ:FAST,NASDAQ:FB,NYSE:FDX,NASDAQ:FEYE,NASDAQ:FSLR,NYSE:HD,NYSE:HOME,NASDAQ:JD,NYSE:JNJ,NYSE:KO,NASDAQ:LAZR,NYSE:LMND,NASDAQ:LOGI,NASDAQ:LYFT,NYSE:MCD,NASDAQ:MDB,NYSE:MMM,NASDAQ:MSFT,NASDAQ:MU,NYSE:NET,NASDAQ:NFLX,NYSE:NIO,NASDAQ:NVDA,NASDAQ:PDD,NASDAQ:PENN,NYSE:PINS,NYSE:PLTR,NASDAQ:PLUG,NASDAQ:PTON,NYSE:QS,NASDAQ:ROKU,NASDAQ:RUN,NYSE:SHOP,NYSE:SNAP,NYSE:SPOT,NYSE:SQ,NYSE:TECK,NASDAQ:TLRY,NASDAQ:TSLA,NYSE:TWTR,NYSE:UBER,NYSE:UPS,NASDAQ:UPST,NYSE:W,NASDAQ:WBA,NASDAQ:WDAY,NASDAQ:WIX,NYSE:WMT,NASDAQ:ZM";
  var str = `<div class="columns">`;
  list.split(",").map((item, cnt) => {
    str += `<div class="column is-one-fourth"><a href="https://tradingview.com/symbols/${
      item.split(":")[1]
    }">${item.split(":")[1]}</a></div>`;
    if (cnt != 0 && (cnt + 1) % 6 == 0) {
      str += `</div><div class="columns">`;
    }
  });
  str += "</div>";
  return { __html: str };
}

