import { BrowserRouter } from "react-router-dom";
import React, { useState } from 'react';
import ReactMarkdown from 'react-markdown'
import Tradies from "./Tradies";

function findNextCount(count, tradie, withAnnotations, next) {
  const nextCount = next ? count+1 : count-1;
  if(next && count >= tradie.steps.length - 1)
    return count;
  if (!next && count == 0) {
    return count;
  }
  if (!withAnnotations && tradie.steps[nextCount].tags && tradie.steps[nextCount].tags.indexOf("annotated") !== -1) {
    //annotated step
    return findNextCount(nextCount, tradie, withAnnotations, next)
  }
  return nextCount;
}



function TradiesSteps(props) {

  let [count, setCount] = useState(0)
  let [learning, setLearning] = useState(true)
  let [annotated, setAnnotated] = useState(false)

  function next(learning, count) {
    if(learning)
      setAnnotated(false)
    setCount(count);
  }

  let eyeSymbol = annotated ? "fas fa-eye" : "fas fa-eye-slash"
  let learningSymbol = learning ? "far fa-lightbulb" : "fa fa-lightbulb"

  return (
    <div>
      {/* start */}
      <div className="columns">
        {/* card */}
        <div className="column">
          <div className="card">
            <header className="card-header">
              <p className="card-header-title has-text-centered">
                Schritt {props.tradie.steps[count].step + 1}: Timeframe {props.tradie.steps[count].timeframe} - ({props.tradie.date} {props.tradie.symbol} {props.tradie.type})
              </p>
            </header>
            <div className="card-image">
              <figure class="image">
                <img
                  src={props.tradie.steps[count].image}
                ></img>
              </figure>
            </div>
            <div className="card-content">
              <div className="content">
               {annotated ? props.tradie.steps[count].description : ""} 
              </div>
            </div>
            <footer className="card-footer">
              <a href="#" onClick={(()=>{next(learning, findNextCount(count, props.tradie, annotated,false))})} class="card-footer-item">
                <span className="icon">
                  <i aria-hidden="true" className="fas fa-backward"></i>
                </span>
              </a>
              <a href="#" onClick={
                (()=>{next(learning, findNextCount(count, props.tradie, annotated,true))})
              } class="card-footer-item">
                <span className="icon">
                  <i aria-hidden="true" className="fas fa-forward"></i>
                </span>
              </a>
              <a href="#" onClick={(()=>{setAnnotated(!annotated)})} class="card-footer-item">
                <span className="icon">
                  <i aria-hidden="true" className={eyeSymbol}></i>
                </span>
              </a>
              <a href="#" onClick={(()=>{next(0)})} class="card-footer-item">
                <span className="icon">
                  <i aria-hidden="true" className="fas fa-history"></i>
                </span>
              </a>
              <a href="#" onClick={(()=>{setLearning(!learning)})} class="card-footer-item">
                <span className="icon">
                  <i aria-hidden="true" className={learningSymbol}></i>
                </span>
              </a>
            </footer>
          </div>
        </div>
        
        {/* description */}
      </div>
    </div>
  );
}

export default TradiesSteps;
