import { BrowserRouter } from "react-router-dom";
import TradiesAside from "./TradiesAside";
import TradiesSteps from "./TradiesSteps"
import amd from "./plays/20210405_amd.json"
import TradiesModal from "./TradiesModal";
import { useState } from "react";


function Tradies() {

  const [modalActive, setModalActive] = useState(false);
  const [currentTradie, setCurrentTradie] = useState(amd)

  function showTradie(tradie) {
    setCurrentTradie(tradie)
    setModalActive(true)
  }
  
  return (
    <div>
    <div className="columns">
      <div className="column is-three-quarters">
        <div className="card">
          <header className="card-header">
            <p className="card-header-title has-text-centered">Tradies</p>
          </header>

          <div className="card-content">
            <div className="content">
            <a href="#" onClick={(()=>{showTradie(amd)})}>
                <span className="icon">
                  <i aria-hidden="true" className="fas fa-history"></i>
                </span>
                AMD
              </a>
            </div>
          </div>
          <footer className="card-footer"></footer>
        </div>
      </div>
      <div className="column is-one-quarter">
        <TradiesAside></TradiesAside>
      </div>
    </div>
    <TradiesModal activate={setModalActive} active={modalActive} title={currentTradie.symbol}>
         <TradiesSteps tradie={currentTradie}></TradiesSteps>
    </TradiesModal>
    </div>

  );
}

export default Tradies;
